import axios from 'axios';
import React, {useState} from "react"
import {useTranslation} from "react-i18next";

function Form() {
    const GETFORM_FORM_ENDPOINT = "https://getform.io/f/5854d559-b03a-450b-83c1-c5d1bae2de26";
    const {t} = useTranslation();
    const [formStatus, setFormStatus] = useState(false);
    const [query, setQuery] = useState({
        name: "",
        email: "",
        phone: "",
        about: "",
    });

    const handleFileChange = () => (e) => {
        const name = e.target.name;
        setQuery((prevState) => ({
            ...prevState,
            [name]: e.target.files[0]
        }));
    };

    const handleChange = () => (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setQuery((prevState) => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const formData = new FormData();
        Object.entries(query).forEach(([key, value]) => {
            formData.append(key, value);
        });

        axios
            .post(
                GETFORM_FORM_ENDPOINT,
                formData,
                {headers: {Accept: "application/json"}}
            )
            .then(function (response) {
                setFormStatus(true);
                setQuery({
                    name: "",
                    email: "",
                    phone: "",
                    about: "",
                });
                console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
    };
    return (
        <div className="relative pt-5 justify-center ">
            <div className="md:container mx-auto px-4 h-full flex flex-col justify-center md:items-center pt-24">
                <h1 className="text-center text-3xl md:text-5xl glitch" data-glitch={t('work_for_us')}>
                    {t('work_for_us')}
                </h1>

                <div className="flex flex-col-reverse lg:flex-row sm:pt-0 sm:-mx-4 md:py-4  w-full lg:w-2/4">
                    <form method="post" onSubmit={handleSubmit} className="w-full mb-20 sm:mb-0">
                        <div className="grid grid-cols-1 gap-4">
                            <div>
                                <input className="css--form-style" name="name" value={query.name} type="text" placeholder={t('your_name')} required autoComplete="off" onChange={handleChange()}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="email" value={query.email} type="email" placeholder={t('email')} required autoComplete="off" onChange={handleChange()}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="phone" value={query.phone} type="tel" placeholder={t('phone')} autoComplete="off" onChange={handleChange()}/>
                            </div>
                        </div>
                        <div className="grid grid-cols-1 gap-4 pt-4">
                            <textarea className="css--form-style" name="about" value={query.about} placeholder={t('about_yourself')} required onChange={handleChange()}/>
                        </div>
                        <div className="grid grid-cols-1 gap-4 pt-4">
                            <div className="css--form-style">
                                <label htmlFor="CVUpload" className="w-full flex"><img className="w-3.5 mr-4" alt="Upload Icon" src="/images/icons/upload-icon.svg" height="100%" width="100%"/> {t('your_cv')}</label>
                                <input className="hidden" id="CVUpload" name="cv" type="file" placeholder={t('your_cv')} required autoComplete="off" onChange={handleFileChange()}
                                />
                            </div>
                            <div className="css--form-style">
                                <label htmlFor="mletter" className="flex"><img className="w-3.5 mr-4" alt="Upload Icon" src="/images/icons/upload-icon.svg" height="100%" width="100%"/>{t('motivational_letter')}</label>
                                <input className="hidden" id="mletter" name="motivational" type="file" placeholder={t('motivational_letter')} required autoComplete="off" onChange={handleFileChange()}/>

                            </div>
                        </div>
                        {formStatus &&
                            <div className="text-center my-4">
                                Your message has been sent.
                            </div>
                        }
                        <div className="grid md:grid-cols-4 sm:grid-cols-1 my-4 text-center">
                            <button className="btn px-5" type="submit">{t('submit')}</button>
                        </div>
                    </form>
                </div>

            </div>
        </div>

    );
}

export default Form