import '../i18n/i18n';
import React from "react"
import FadedText from "../components/elements/FadedText";
import Navigation from "../components/layout/Navigation";
import {SEO} from "../components/seo";
import CareerForm from "../components/layout/Career/Career-form";

export default function ContactUs() {
    return (
        <main className="main-wrapper">
            <Navigation pageNr={'2ET9W'}/>
            <FadedText text={'career@kalmars.lv'} direction="bottom"/>
            <section className="min-h-screen relative">
                <CareerForm/>
            </section>
        </main>

    )
}
export const Head = () => <SEO title={"Career"} description={'Are you looking for stable work place? We are looking for React, Laravel, React Native, Vue, Unity, Unreal engine developers!'}/>

